import React from 'react';
import YAMLData from '../../content/contact.yaml';

// social media icons
const onClickSosmedEvent = (sosmed) => {
    window.open(YAMLData[sosmed]);
}
const onKeyDownSosmedEvent = (e, sosmed) => {
    if (e.key === 'Enter') {
        onClickSosmedEvent(sosmed);
    }
}

